
import { defineComponent, computed } from 'vue';
import { useStore } from 'vuex';
import { comma } from '@/utils/filters';

export default defineComponent({
  name: 'CovidTotalItem',
  setup() {
    const { state } = useStore();
    const covidTotal = computed(() => state.covidList.covidTotal);

    return {
      covidTotal,
      comma,
    };
  },
});
